<template>
  <div class="container">
    <HeadTop :title="$t('index.啟動礦工')" :isSubHeader="true"></HeadTop>
    <div class="join">
      <img class="img" src="@/assets/images/join@2x.png" alt="">
      <div class="des">{{$t('index.會員激活需要立刻支付key，支付成功後，可立即獲得power算力開啟質押挖礦',{key:registerFee+'USDT', power: power})}}</div>
      <!-- <div class="des">恭喜您激活成功，你可以參與質押挖礦了。</div> -->
      <div class="btn-wrap">
         <van-button v-if="isActive" class="btn" disabled>{{$t('index.已啟動')}}</van-button>
        <van-button v-else-if="Number(usdtAllowance) > 0" class="btn" :loading="btnLoading" size="large" :loading-text="$t('index.一鍵激活') + '...'" @click="active">{{$t('index.一鍵激活')}}</van-button>
        <van-button :loading="btnLoading" size="large" :loading-text="$t('index.授權') + '...'" v-else class="btn" @click="approveUsdt">{{$t('index.授權')}}</van-button>
      </div>
    </div>
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop'
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import UvStakeAbi from "@/utils/UvStakeAbi";
import { Toast } from 'vant';
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
export default {
  components: {
    HeadTop
  },
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      imgSrc: '',
      name: '',
      token: '',
      desc: '',
      isPreview: false,
      showDialog: false,
      UvStakeContract: null,
      UsdtContract: null,
      usdtAllowance: 0,
      registerFee: '0',
      btnLoading: false,
      power: 0,
      isActive: false,
      initContract: () => {
        data.UvStakeContract = new data.web3.eth.Contract(UvStakeAbi, config.uvStakeAddress)
        data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.usdtAddress)
        data.initData()
      },
      initData: () => {
        data.getUsdtAllowance()
        data.getActiveFee()
      },
      getActiveFee: async () => {
        let decimals = await data.UsdtContract.methods.decimals().call();
        let userInfo = await data.UvStakeContract.methods.getUserInfo(data.address).call()
        let usdtBalance = await data.UsdtContract.methods.balanceOf(data.address).call()
        let registerBasePower = await data.UvStakeContract.methods.registerBasePower().call()
        let addUserTime = await data.UvStakeContract.methods.addUserTime().call()
        let power = await data.UvStakeContract.methods.getIncrease(registerBasePower, addUserTime).call()
        
        if(addUserTime == 0) {
          data.power =  new BigNumber(registerBasePower).dividedBy(10 ** 18).toFixed(3)     
         } else {
           data.power = new BigNumber(power).dividedBy(10 ** 18).toFixed(3)
         }
        // data.power = new BigNumber(power).dividedBy(10 ** 18).toFixed(3)
        data.isActive = userInfo.isExist
        let fee = await data.UvStakeContract.methods.registerFee().call()
        data.registerFee = new BigNumber(fee).dividedBy(10 ** decimals).toFixed(4)
        data.balance = new BigNumber(usdtBalance).dividedBy(10 ** decimals).toFixed(4)
      },
      getUsdtAllowance: async () => {
        let result = await data.UsdtContract.methods.allowance(data.address, config.uvStakeAddress).call();
        data.usdtAllowance = result;
      },
      approveUsdt: async () => {
        data.btnLoading = true
        try {
           await data.UsdtContract.methods.approve(config.uvStakeAddress, MaxUint256).send({ from: data.address });
        data.getUsdtAllowance()
        data.btnLoading = false
        } catch (error) {
          data.btnLoading = false
        }
       
      },
      active: async () => {
         if(Number(data.balance) < Number(data.registerFee)) {
           Toast(data.$t('index.餘額不足'))
           return
         }
         data.btnLoading = true
        try {
           await data.UvStakeContract.methods.register().send({ from: data.address });
        data.initData()
        Toast(data.$t('index.激活成功'))
        data.btnLoading = false
        } catch (error) {
          data.btnLoading = false
        }
       
      }
    })
    if (data.address) {
      data.initContract()
    }
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.container {
  background: #fff;
  min-height: 100vh;
  overflow: auto;
  padding-top: 90px !important;
}
.join {
  padding: 17px 16px 16px;
  .img {
    width: 175px;
    display: block;
    margin: 0 auto;
  }
  .des {
    font-size: 16px;
    color: @deep-color;
    word-break: break-all;
    text-align: center;
  }
  .btn-wrap {
    padding-top: 28px;
    .btn {
      background: @theme-color;
      height: 48px;
      color: #fff;
    }
  }
}
</style>
<style lang="less">
</style>
